body {
    margin: 0;
    height: 100%;
}

.weather_wrapper__top {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: white;
    margin: 0 auto;
    padding: 1% 0;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.weather_wrapper__top_left {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 300px;
}

.weather_wrapper__top_main_temp {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 50%;
}

.weather_wrapper__top_main_temp_wrapper {
    display: flex;
    flex-direction: column;
    width: 50%;
}

.weather_wrapper__top_main_temp_value {
    width: 50%;
    font-size: 80px;
    margin: 0 auto;
    text-align: center;
}

.weather_wrapper__top_main_temp_description {
    width: 50%;
    font-size: 35px;
    margin: 0 auto;
    text-align: center;
}

.weather_wrapper__top_date {
    display: flex;
    justify-content: start;
    font-size: 30px;
    width: 50%;
}
.weather_wrapper__top_location {
    display: flex;
    justify-content: start;
    font-size: 40px;
    width: 50%;
}

.weather_wrapper__top_right {
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    width: 685px;
    /* height: 25vh; */
    padding: 15px 15px;
    margin: 20px;
    background-color: rgb(179,179,179, 0.3);
    border: 1px solid rgb(179, 179, 179);
    border-radius: 5px;
}

/* ___________________________________________________________________ */
.weather_wrapper__bottom {
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
    padding-top: 15px;
}

/* ___________________________________________________________________ */
.weather_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-image: url('./assets/background_new.jpg');
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    /* background-size: 100%; */
    background-size: cover;
    border-radius: 5px;
    min-height: 100vh;
    /* overflow: hidden; */
}
.listElem_wrapper {
    margin: 0 auto;
    width: 200px;
    padding: 1px;
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(179, 179, 179);
    color: white;
    border-radius: 5px;
    align-items: center;
    z-index: 999;
    background-color: rgb(179,179,179, 0.45);;
}

.listElem_wrapper:hover {
    cursor: poiner;
}

@media (max-width: 900px) { 
    .weather_wrapper__top {
        gap: 100px;
    }
}