.header {
    /* width: 100%; */
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 5px solid rgb(19, 50, 73);
    font-size: 23px;
    z-index: 500;
  }
  .navbar {
    width: 80vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .navbar__links {
    width: 50vw;
    font-size: 23px;
    display: flex;
    justify-content: space-between;
  }
  .navbar__link_button {
    font-size: 23px;
    font-family: 'Times New Roman', Times, serif;
    background-color: inherit;
    text-decoration: none;
    color: rgb(199, 199, 199);
    border: 1px solid rgb(199, 199, 199);
    padding: 10px;
    border-radius: 5px;
  }
  .navbar__link_button:hover {
    cursor: pointer;
    background-color:rgb(48, 113, 163);
  }
  .catalog {
    display: none;
  }
  .navbar__link {
    text-decoration: none;
    color: rgb(199, 199, 199);
  }
  .out {
    margin-right: 15px;
  }
  .navbar__user_login {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  @keyframes show {
    from {right: -100%;}
    to {right: 0%;}
  }

  @media (max-width: 768px) {
    .navbar__link {
      color: rgb(199, 199, 199);
    }
    .header {
      justify-content: flex-start;
      padding: 1% 5%;
    }
    .navbar {
      display: none;
      z-index: 100;
      position: absolute;
      background-color: rgb(19, 50, 73, 0.95);
      right: -100%;
    }
    .navbar.active {
      height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      animation-name: show;
      animation-duration: 0.5s;
      top: 0%;
      right: 0%;
      color: rgb(199, 199, 199);
    }
    .navbar.active > .navbar__links {
      margin: 0 auto;
      margin-top: 15%;
      height: 30%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      gap: 25px;
    }
  }

  @media (max-width: 390px) {
    .navbar__links {
      width: 80vw;
    }
  }