.block_shedule_wrapper {
    width: 400px;
    padding: 0.5rem;
    border: 1px solid rgb(199, 199, 199);;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    color: rgb(199, 199, 199);;
    background-color: rgb(54, 82, 104);
}
.title_shedule {
    padding: 5px;
    font-size: 20px;
    width: 100%;
    text-align: center;
    border-top: 3px solid rgb(19, 50, 73);
    border-bottom: 3px solid rgb(19, 50, 73);
}