/* ------------------------------------------------------------------------------ */
.data_wrapper_card {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
    width: 200px;
    border: 1px solid rgb(179,179,179, 0.5);
    border-radius: 5px;
    flex-wrap: wrap;
}

.data_wrapper_card_value, .data_wrapper_card_text{
    text-align: center;
    font-size: 17px;
}