.wrapper_for_back {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 10, 17, 0.75);
    display: flex;
    justify-content: center;
    align-items: center;
}

label {
    color: rgb(199, 199, 199);
}

select {
    min-width: 200px;
    text-align: center;
    padding: 5px;
    font-size: 16px;
    border: 1px solid rgb(199, 199, 199);
    border-radius: 5px;
    background-color: rgb(199, 199, 199);
    appearance: none;
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6"><path fill="%23333" d="M1 1l4 4 4-4"/></svg>');
    background-repeat: no-repeat;
    background-position: right 10px center;
    cursor: pointer;
}

select:focus {
    border-color: #6C757D;
    outline: none;
}

.add_task_wrapper {
    min-width: 350px;
    height: 350px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    border: 1px solid rgb(199, 199, 199);
    border-radius: 10px;
    background-color: rgb(19, 50, 73, 0.9);
}
.shedule_input {
    width: 70%;
    border: none;
    border-bottom: 1px solid rgb(199, 199, 199);;
    color: rgb(199, 199, 199);;
}
.shedule_input::placeholder {
    color: rgb(199, 199, 199);;
}
.shedule_input:focus {
    outline: none;
}
.buttons_wrapper {
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

