.block_inputs {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.block_buttons {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.task_input, .description_input {
    width: 100%;
    outline: none;
}

.description_input {
    resize: none;
}

.shedule__add_button {
    border: 0.5px solid rgb(48, 113, 163);
    background-color: inherit;
    color: rgb(199, 199, 199);
    border-radius: 15px;
    padding: 10px;
}
.shedule__add_button:hover {
    cursor: pointer;
    background-color:rgb(48, 113, 163);
}