body {
    background-color: rgba(71, 81, 89);
}
.shedule_wrapper {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: space-evenly;
    align-items: center;
}
.shedule_title {
    width: 100%;
    padding-bottom: 1vh;
    text-align: center;
    margin: 0 auto;
    color: rgb(199, 199, 199);
    border-bottom: 3px solid rgb(19, 50, 73);
}
.shedule_blocks {
    display: flex;
    justify-content: space-evenly;
    gap: 15px;
    width: 95%;
    flex-wrap: wrap;
}

.shedule__add_button {
    width: 200px;
    margin: 0 auto;
    background: none;
    border: 1px solid rgb(199, 199, 199);
    border-radius: 5px;
}

.shedule__add_button:hover {
    cursor: pointer;
    background-color:rgb(48, 113, 163);
}

.shedule__add_button {
    font-size: 20px;
    border: 2.5px solid rgb(48, 113, 163);
    background-color: inherit;
    color: rgb(199, 199, 199);
    border-radius: 15px;
    padding: 10px;
}