.block_wrapper {
    padding: 15px;
    border: 0.5px solid rgb(199, 199, 199);
    border-radius: 10px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    background-color: rgb(19, 50, 73, 0.7);
}

.block_inputs {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.block_buttons {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 auto;
}

.button_card:hover {
    cursor: pointer;
    background-color:rgb(48, 113, 163);
}

.task_input, .description_input, .task_input_active, .description_input_active {
    padding: 5px;
    width: 100%;
    outline: none;
    background-color: inherit;
    border: none;
    border: 0.5px solid rgb(48, 113, 163);
    border-radius: 5px;
    color: rgb(199, 199, 199);
}

.task_input_active:focus, .description_input_active:focus {
    background-color: rgb(199, 199, 199);
    color: rgb(19, 50, 73);
}

.task_input:hover, .description_input:hover, .task_input_active:hover, .description_input_active:hover {
    cursor: pointer;
}

.description_input, .description_input_active {
    resize: none;
}

.button_card {
    border: 0.5px solid rgb(48, 113, 163);
    background-color: inherit;
    color: rgb(199, 199, 199);
    border-radius: 15px;
    padding: 10px;
}