.slider-container {
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.slider-content {
    width: 85%;
    margin: 0 auto;
    overflow: hidden;
    display: flex;
    gap: 25px;
    transition: transform 0.5s ease-in-out;
}

.slider-item {
    font-size: 17px;
    margin: 0;
    min-width: 200px;
    transition: transform 0.5s ease-in-out;
}

.slider-item:hover {
    cursor: pointer;
}

.date-content {
    display: flex;
    flex-direction: column;
}

.slider-button {
    background-color: inherit;
    color: white;
    border: none;
    padding: 10px;
}
.slider-button:hover {
    cursor: pointer;
}

.slider-button.prev {
    left: 10px;
    z-index: 999;
}

.slider-button.next {
    right: 10px;
    z-index: 999;
}
.slider-item-count {
    border: 1px solid rgb(179, 179, 179);
    position: absolute;
    top: 81.5%;
    right: 0%;
    padding: 5px;
    border-radius: 5px;
    font-size: 15px;
    color: rgb(179, 179, 179);
}

@media (max-width: 900px) {
    .slider-button {
        display: none;
    }
    .slider-content {
        overflow-x: auto;
        scrollbar-width: thin;          /* "auto" или "thin"  */
        scrollbar-color: rgb(0, 0, 29) rgb(179, 179, 179);
    }
}