/* Login.css */
.wrapper_for_back {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 10, 17, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-container {
  min-width: 450px;
  height: 450px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  border: 1px solid rgb(199, 199, 199);
  border-radius: 10px;
  background-color: rgb(19, 50, 73, 0.9);
}

