    justify-content: center;
    height: 70vh;
    background-color: rgba(71, 81, 89);
  } */
  
  .buttons_wrapper {
    margin: 0 auto;
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

  .login-title {
    color: rgb(199, 199, 199);;
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .login-error {
    color: rgb(255, 153, 153);
    margin-bottom: 10px;
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
    width: 300px;
    padding: 20px;
    background-color: rgb(199, 199, 199);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .login-input-group {
    margin-bottom: 15px;
  }
  
  .login-label {
    color: rgb(19, 50, 73);
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .login-input {
    width: 90%;
    padding: 10px;
    outline: none;
    background-color: inherit;
    border: none;
    border: 0.5px solid rgb(48, 113, 163);
    border-radius: 5px;
    color: rgb(19, 50, 73);
  }

  .login-input:-webrit-autofill {
    background-color: rgb(199, 199, 199) !important;
  }

  .login-input:focus {
    background-color: rgb(199, 199, 199);
    color: rgb(19, 50, 73);
}
  
  .login-button {
    padding: 10px;
    background-color: rgb(19, 50, 73, 0.7);
    color: rgb(199, 199, 199);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .login-button:hover {
    background-color: rgb(48, 113, 163);
    color: #fff;
  }

