.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}

.loader__dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #3498db;
  margin: 0 5px;
  animation: bounce 1.4s infinite ease-in-out;
}

.loader__dot:nth-child(2) {
  animation-delay: 0.2s;
}

.loader__dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes bounce {
  0%, 80%, 100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}